body {
  font-family: "Titillium Web", sans-serif;
  line-height: 1.4;
  padding: 54px 0;
  background: url("../assets/bg.png") no-repeat top/cover;
  position: relative;
}

/*  Global classes and variables */
.form__title {
  font-size: 2em;
  font-weight: 300;
  color: #fff;
  padding: 25px 0;
  text-align: center;
  text-shadow: 1px 1px 2px #000;
}

.form__form {
  background-color: #f4f4f4;
  padding: 20px;
  background-clip: padding-box;
  border: 20px solid rgba(0, 0, 0, 0.3);
  border-radius: 6px;
}

.form__field {
  border: 1px solid #c6c6c6;
  border-radius: 5px;
  height: 40px;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.form__field--textarea {
  height: 230px;
}

.form__field:focus-within {
  background: #fff;
  border-color: #7ec31f;
}

.form__field .fas {
  font-size: 1.4em;
  color: #dedede;
  flex-basis: 50px;
  text-align: center;
  border-right: 1px solid #c6c6c6;
}

.form__field input,
.form__field select,
.form__field textarea {
  flex: 1;
  height: 100%;
  padding-left: 22px;
  border: none;
  font-family: inherit;
  background-color: transparent;
  color: #afafaf;
  font-weight: 300;
}

.form__field textarea {
  resize: none;
  padding-top: 10px;
}

.form__field select {
  padding-left: 18px;
  color: #afafaf;
}

.form__field label {
  display: none;
}

input[type="checkbox"] {
  display: none;
}

input[type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
}

input[type="checkbox"]:checked + label:after {
  opacity: 1;
}

input[type="checkbox"] + label {
  display: block;
  position: relative;
  color: #afafaf;
  font-size: 1em;
  font-weight: 300;
  padding-left: 2.5em;
  cursor: pointer;
}

input[type="checkbox"] + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 24px;
  height: 24px;
  border: 1px solid #c6c6c6;
  border-radius: 3px;
  background: #f6f6f6;
}

input[type="checkbox"] + label:after {
  content: "";
  position: absolute;
  top: 6px;
  left: 6px;
  width: 14px;
  height: 14px;
  background: -webkit-gradient(linear, left top, left bottom, from(#a3d840), to(#7ec31f));
  background: linear-gradient(#a3d840, #7ec31f);
  border-radius: 3px;
}

.btn--primary, .btn--close {
  display: inline-block;
  padding: 0.3em 2em;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  border: none;
  cursor: pointer;
}

.btn--primary:disabled, .btn--close:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.btn--primary {
  color: #333;
  width: 100%;
  background: linear-gradient(#a3d840, #7ec31f);
  text-shadow: 1px 1px 2px #2c6a02;
  border: 1px solid #3b9a00;
  border-radius: 5px;
  color: #fff;
  font-family: inherit;
  font-size: 1.5em;
}

.header {
  text-align: center;
}

.header__logo {
  opacity: 0.5;
}

main .form-grid {
  columns: 2;
  column-gap: 80px;
}

main .form-grid .form {
  max-width: 500px;
  margin: auto;
  break-inside: avoid;
}

main .form-grid .form-register .form__submit,
main .form-grid .form-send .form__submit {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 30px;
}

main .form-grid .form-f img {
  opacity: 0.5;
}

@media (max-width: 1050px) {
  .header__logo {
    width: 70%;
  }
  main .form-grid {
    columns: unset;
  }
}

@media (max-width: 540px) {
  .container {
    padding: 0;
  }
  main .form-grid {
    columns: 1;
  }
}
